import Star from './common/starComp/StarZoom'
export default {
  name: 'ProductOrder',
  components: {
    Star
  },
  props: {
    info: {
      type: Object,
      default () {
        return {}
      }
    },
    companyInfo: {
      type: Object,
      default () {
        return {}
      }
    },
    categoryId: {
      type: String,
      default () {
        return ''
      }
    },
    categoryName: {
      type: String,
      default () {
        return ''
      }
    }
  },
  methods: {
    contact () {
      this.$emit('formShow', this.info.qcid)
    },
    navigateToDetail (info) {
      let routeUrl = this.$router.resolve({
        name: 'productDetail-id',
        params: { id: info.product_id }
      })
      window.open(routeUrl.href, '_blank')
    }
  }
}
